html {
  background: #f6f6f6; }

body {
  font-family: Roboto, Helvetica, "Luicida Sans", sans-serif;
  font-size: 16px;
  color: #636363; }

#app {
  background-color: #fff;
  margin: 20px; }

.section {
  padding: 0.5rem 0.5rem 0; }

h2,
.main-heading {
  font-size: 2em; }

h3 {
  font-family: Roboto, Helvetica, "Luicida Sans", sans-serif;
  font-size: 1.4em; }

.profile-pic__container {
  margin: 0 2em 2em 0; }

.profile-pic {
  border-radius: 50%;
  animation: pic 0.4s;
  transform-origin: center;
  filter: grayscale(0);
  opacity: 0.75; }

@keyframes pic {
  from {
    transform: scale(0); }
  to {
    transform: scale(1); } }

.icons {
  margin-top: 16px;
  height: 24px; }

.icons > * {
  margin-left: 16px; }

.is-contact {
  text-align: right;
  font-size: 0.9em; }
  .is-contact > p {
    margin-bottom: 6px; }

.title__container {
  text-align: center; }

.title {
  font-family: Roboto, Helvetica, "Luicida Sans", sans-serif;
  font-size: 3em;
  color: #333333; }

.achievements {
  margin-bottom: 20px; }

.achievements--list p {
  margin: 12px 0;
  line-height: 1.75; }

.skill-cloud {
  margin-top: 20px; }

.tech-used,
.skill-cloud {
  font-size: 0.85em; }

.tech-used span,
.skill-cloud span {
  margin: 5px 5px 5px 0;
  padding: 3px 6px;
  background: #f2f2f2;
  border-radius: 3px;
  display: inline-block; }

.experience-holder,
.education-holder {
  border-left: 0.5em solid #f0f0f0;
  margin: 2em 0;
  padding: 0 0 0 1em;
  line-height: 1.75em; }

.role__container {
  margin-bottom: 20px;
  page-break-inside: avoid; }

.team-title {
  margin-bottom: 10px;
  color: #19bb4a; }

.location {
  color: #19bb4a; }

.date {
  font-weight: bold; }

.achievements--list h3,
.company,
.job-title {
  font-size: 1.3em;
  color: #0073c0; }

.job-title {
  margin-bottom: 25px; }

.achievements--list h3 {
  margin: 20px 0 0;
  padding: 0; }

.job-desc__bullets {
  list-style: disc inside; }

/* Media Queries */
@media only screen {
  .cv-only {
    display: none; }
  .pdf.cv-only {
    display: block; } }

@media only screen and (min-width: 800px) {
  .is-vertical-center {
    display: flex;
    align-items: center; }
  .profile-pic {
    max-width: 150px; }
  .is-vertical-center {
    display: flex;
    align-items: center; }
  .profile-pic {
    max-width: 150px; } }

@media only screen and (max-width: 799px) {
  .profile-pic__container {
    text-align: center;
    margin: 0;
    width: auto; }
  .title {
    font-size: 2rem; }
  .is-contact {
    text-align: center;
    font-size: 1.1rem;
    font-weight: bold; }
  .fab {
    margin: 1rem 1rem 0; }
  .experience-holder,
  .education-holder {
    border-left: 0;
    padding: 0; } }

@media only print {
  html {
    background-color: #ffffff; }
  .profile-pic {
    max-width: 150px; } }
